export default {
  name: "center",
  computed:{
      loginname:function () {
        return this.$store.state.loginname;
      }
  },
  created:function () {
    //页面执行成功之后，直接执行页面跳转
    //replace 表示代替当前的位置
    this.$router.replace('/user/' + this.loginname);
  }

}



